@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}

.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}

body {
  background-color: green;
}

.promo {
  background-color: red;
  height: 500px;
}

.footer {
  background-color: black;
  height: 100px;
}

.main-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.main-header__container {
  padding-top: 35px;
  padding-bottom: 35px;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .main-header__container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

.main-header__logo {
  margin-right: auto;
}

@media (max-width: 767px) {
  .main-header__nav {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-header--no-js.main-header {
    position: static;
  }
}

@media (max-width: 767px) {
  .main-header--no-js .main-header__nav,
.is-menu-opened .main-header__nav {
    position: static;
    display: block;
    width: 100%;
    margin-top: 20px;
    order: 2;
  }
}

@media (max-width: 767px) {
  .main-header--no-js .main-nav__item,
.is-menu-opened .main-nav__item {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .main-header--no-js .main-nav__link,
.is-menu-opened .main-nav__link {
    color: #011c40;
    text-align: left;
  }
  .main-header--no-js .main-nav__link:hover,
.is-menu-opened .main-nav__link:hover {
    color: #ffd740;
  }
}

@media (max-width: 767px) {
  .main-header--no-js .main-header__button-toogle {
    display: none;
  }
}

@media (max-width: 767px) {
  .is-menu-opened .main-header__container {
    position: absolute;
    top: 0;
    left: 30px;
    right: 0;
    padding-left: 15px;
    padding-right: 15px;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .main-header--no-js .logo__img,
.is-menu-opened .logo__img {
    fill: #011c40;
  }
}

@media (max-width: 767px) {
  .is-menu-opened .button-toogle__img-open {
    display: none;
  }
}

@media (max-width: 767px) {
  .is-menu-opened .main-header__nav {
    min-height: 500px;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .is-menu-opened .button-toogle__img-close {
    display: block;
  }
}

.logo__img {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .logo__img {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__img {
    width: 42px;
    height: 22px;
  }
}

.main-nav {
  max-width: 670px;
}
@media (max-width: 1023px) {
  .main-nav {
    max-width: 600px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    max-width: none;
  }
}

.main-nav__list {
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  margin: -5px -16px;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
    align-items: start;
    margin-left: 0;
    margin-right: 0;
  }
}

.main-nav__item {
  margin: 5px 16px;
  max-width: 190px;
}
@media (max-width: 767px) {
  .main-nav__item {
    margin-left: 0;
    margin-right: 0;
    max-width: none;
  }
}

.main-nav__link {
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
  font-weight: 500;
  font-size: 16px;
  color: #f9fbfd;
  text-align: center;
  transition: color 0.3s ease;
}
@media (max-width: 1023px) {
  .main-nav__link {
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 14px;
    line-height: 17px;
  }
}
.main-nav__link:hover {
  color: #ffd740;
}
.main-nav__link:focus-visible {
  color: #ffd740;
  outline: 1px solid #ffd740;
  outline-offset: 2px;
}
.main-nav__link:active {
  opacity: 0.7;
}

.button-toogle {
  display: none;
  padding: 5px;
  padding-right: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  appearance: none;
}
@media (max-width: 767px) {
  .button-toogle {
    display: block;
  }
}
.button-toogle:hover .button-toogle__img-open, .button-toogle:hover .button-toogle__img-close {
  stroke: #ffd740;
}
.button-toogle:focus-visible {
  outline: 1px solid #ffd740;
  outline-offset: 2px;
}
.button-toogle:active {
  opacity: 0.7;
}
.button-toogle:disabled .button-toogle__img-open, .button-toogle:disabled .button-toogle__img-close {
  stroke: #9d9d9d;
  pointer-events: none;
}

.button-toogle__img-open {
  stroke: #f9fbfd;
  transition: stroke 0.3s ease;
}

.button-toogle__img-close {
  display: none;
  stroke: #011c40;
  transition: stroke 0.3s ease;
}

